import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '',
    component: LayoutComponent,
    data: {
      title: 'Home'
    },
    children: [ 
      {
        path: 'createInvoice',
        loadChildren: () => import('./views/invoice/createInvoice').then(m => m.CreateInvoiceModule)
      },
      {
        path: 'viewInvoices',
        loadChildren: () => import('./views/invoice/viewInvoices').then(m => m.ViewInvoicesModule)
      },
      {
        path: 'createQuotation',
        loadChildren: () => import('./views/quotation/createQuotation').then(m => m.CreateQuotationModule)
      },
      {
        path: 'viewQuotations',
        loadChildren: () => import('./views/quotation/viewQuotations').then(m => m.ViewQuotationsModule)
      },
      {
        path: 'createStatement',
        loadChildren: () => import('./views/statement/createStatement').then(m => m.CreateStatementModule)
      },
      {
        path: 'viewStatements',
        loadChildren: () => import('./views/statement/viewStatements').then(m => m.ViewStatementsModule)
      },
      {
        path: 'addProduct',
        loadChildren: () => import('./views/productsAndServices/addProduct').then(m => m.AddProductModule)
      },
      {
        path: 'addProductType',
        loadChildren: () => import('./views/productsAndServices/addProductType').then(m => m.AddProductTypeModule)
      },
      {
        path: 'viewProducts',
        loadChildren: () => import('./views/productsAndServices/viewProducts').then(m => m.ViewProductsModule)
      },
      {
        path: 'addService',
        loadChildren: () => import('./views/productsAndServices/addService').then(m => m.AddServiceModule)
      },
      {
        path: 'viewServices',
        loadChildren: () => import('./views/productsAndServices/viewServices').then(m => m.ViewServicesModule)
      },
      {
        path: 'addCustomer',
        loadChildren: () => import('./views/customers/addCustomer').then(m => m.AddCustomerModule)
      },
      {
        path: 'viewCustomers',
        loadChildren: () => import('./views/customers/viewCustomers').then(m => m.ViewCustomersModule)
      },
      {
        path: 'dashboard',
        loadChildren: () => import('./views/dashboard').then(m => m.DashboardModule)
      },
      {
        path: 'settings',
        loadChildren: () => import('./views/settings').then(m => m.SettingsModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SystemRoutingModule { }
