import { DocType } from './../../enums/enums';
import { Component } from '@angular/core';
import {
  NavigationCancel,
  NavigationEnd,
  NavigationError,
  Router,
} from '@angular/router';
import * as $ from 'jquery';
import { takeWhile } from 'rxjs/operators';
import { UserService } from '../../_services/_user.service';

@Component({
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.css'],
})
export class LayoutComponent {
  constructor(private router: Router, private userService: UserService) {}

  sideBarOpen = true;
  sideBarClosed = false;
  SideBarCloseDelay = 400;
  routerSubscription;

  public HomeNav() {
    this.router.navigate(['/system/dashboard']);
  }

  public routerNav(route) {
    if (this.router.url === route) {
      this.DelayToggle();
    } else {
      this.ToggleSideBarAfterRouteEnd();
      this.router.navigate([route]);
    }
  }

  public Home() {
    const route = '/system/dashboard';
    this.routerNav(route);
  }

  public CreateInvoice() {
    const route = '/system/createInvoice';
    this.routerNav(route);
  }

  public ViewInvoices() {
    const route = '/system/viewInvoices';
    this.routerNav(route);
  }

  public CreateQuotation() {
    const route = '/system/createQuotation';
    this.routerNav(route);
  }

  public ViewQuotations() {
    const route = '/system/viewQuotations';
    this.routerNav(route);
  }

  public CreateStatement() {
    const route = '/system/createStatement';
    this.routerNav(route);
  }

  public ViewStatements() {
    const route = '/system/viewStatements';
    this.routerNav(route);
  }

  public AddProduct() {
    const route = '/system/addProduct';
    this.routerNav(route);
  }

  public AddProductType() {
    const route = '/system/addProductType';
    this.routerNav(route);
  }
  public ViewProducts() {
    const route = '/system/viewProducts';
    this.routerNav(route);
  }

  public AddService() {
    const route = '/system/addService';
    this.routerNav(route);
  }

  public ViewServices() {
    const route = '/system/viewServices';
    this.routerNav(route);
  }

  public AddCustomer() {
    const route = '/system/addCustomer';
    this.routerNav(route);
  }

  public ViewCustomers() {
    const route = '/system/viewCustomers';
    this.routerNav(route);
  }

  public Settings() {
    const route = '/system/settings';
    if (this.router.url === route) {
      if ($('#sidebar').hasClass('active')) {
        this.DelayToggle();
      }
    } else {
      if ($('#sidebar').hasClass('active')) {
        this.ToggleSideBarAfterRouteEnd();
      }
      this.router.navigate([]);
    }
  }

  public LogOut() {
    this.userService.Logout();
  }

  private ToggleSideBarAfterRouteEnd() {
    this.router.events
      .pipe(takeWhile((event) => this.IsRouteInProgress(event)))
      .subscribe();
  }

  private IsRouteInProgress(event): boolean {
    switch (true) {
      case event instanceof NavigationEnd:
      case event instanceof NavigationCancel:
      case event instanceof NavigationError: {
        this.DelayToggle();
        return false;
      }
      default: {
        return true;
      }
    }
  }

  public async DelayToggle() {
    await this.Delay(this.SideBarCloseDelay);
    this.ToggleSideBar();
  }

  private Delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  public ToggleSideBar() {
    $('#sidebar').toggleClass('active');
    if ($('#sidebar').hasClass('active')) {
      this.sideBarClosed = true;
      this.sideBarOpen = false;
    } else {
      this.sideBarClosed = false;
      this.sideBarOpen = true;
    }
  }
}
